import Vue from 'vue'
import Rule from './flashsalerule.vue'
let RuleConstructor = Vue.extend(Rule);
let vnode;

const Rules = function (opt){
    opt = opt || {
        title:'活动规则'
    };
    vnode = new RuleConstructor({
        data:opt
    })
    vnode.Curtainshow = true
    vnode.$mount();
    document.body.appendChild(vnode.$el);
    return vnode 
}

export default Rules


