<script>
import {sendSms} from '../../api.config'
export default {
    name:'checkphone',
    data(){
        return{
            show:false,
            count:60,
            focus:false,
            step:1,
            phone:'',
            sms:'',
            testPhone:/^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/
        }
    },
    created(){
        this.bus.$on('checkphone',(res)=>{
            this.show = res  
        })
    },
    watch:{
        sms(){
            if(this.sms.length>6){
                this.sms = this.sms.substr(0,6)
            }
        }
    },
    methods:{
        async nextStep(){
            if(this.testPhone.test(this.phone)){
                // let res = await sendSms({phone:this.phone});
                this.step=2;
                this.TimeOut()
            }else{
                this.$toast({
                    value:'请检查手机号',
                    icon:'frown',
                    mask:true
                })
            }
        },
        TimeOut(){
            this.count = 60;
            let timer = setInterval(()=>{
                this.count-=1
                if(this.count===0){
                    clearInterval(timer)
                }
            },1000)
        }
    }
}
</script>
<template>
    <OMCurtain
        :show="show"
        @close="show=false"
    >
        <div class="check-phone">
            <div class="check-phone_title">验证手机号</div>
            <div class="check-phone_notice">{{step===1?'为了您的信息安全，请验证手机号':`验证码已发送至${phone}`}}</div>
            <transition name="slide" mode="out-in">
                <div class="check-phone_form" v-if="step===1" key="phone">
                    <input 
                    type="text" 
                    class="check-phone_input"
                    placeholder="请输入手机号码"
                    v-model="phone"
                    >
                    <OMButton 
                    size="large"
                    full
                    @click="nextStep"
                    >下一步</OMButton>
                </div>
                <div class="check-phone_form" v-if="step===2" key="sms">
                    <div>
                        <input 
                        type="number" 
                        class="check-phone_input"
                        placeholder="请输入验证码"
                        v-model="sms"
                        style="opacity: 0"
                        id="smscode"
                        @focus="focus=true"
                        @blur="focus=false"
                        autofocus
                        >
                        <label 
                        for="smscode"
                        class="check-phone_smscode">
                            <span 
                            v-for="(i,k) in 6"
                            :key="k"
                            :class="['smscode-input',k===sms.length&&focus&&'smscode-input_active']">{{sms[k]}}</span>
                        </label>
                    </div>
                    <div class="smscodestatus" v-if="count>0">验证码已经发出，{{count}}秒以后重新获取</div>
                    <div class="smscodestatus" v-if="count===0">
                        <span @click="TimeOut">重新获取验证短信</span>
                        <span @click="TimeOut">获取语音验证</span>
                    </div>
                    <OMButton 
                    size="large"
                    full
                    >登录</OMButton>
                </div>
            </transition>
        </div>
    </OMCurtain>
</template>
<style lang="stylus" scoped>
.check-phone
    position relative
    width 5.4rem
    padding .4rem .3rem 
    margin 0 auto 
    background-color #fff
    border-radius .2rem
    overflow hidden
    &_title 
        font-size .32rem
        text-align center
    &_notice
        margin .2rem
        color #666
    &_form
        position relative 
        display inline-block
        width 100%
        .smscodestatus
            margin-bottom .2rem
            text-align left 
            color #409EFF
            display flex 
            justify-content space-between
    &_input 
        width 100%
        height .8rem
        box-sizing border-box
        padding .2rem
        outline none 
        border 1px solid #ccc
        border-radius .1rem
        margin-bottom .2rem
        &::-webkit-input-placeholder{
            color #ccc
        }    /* 使用webkit内核的浏览器 */
        &::-moz-placeholder{
            color #ccc
        }                  /* Firefox版本4-18 */
        &::-moz-placeholder{
            color #ccc
        } 
    &_smscode 
        display flex 
        width 100%
        position absolute
        height .8rem
        top 0
        left 0
        justify-content space-between
        .smscode-input
            position relative
            border 1px solid #aaa
            align-self center
            width .7rem 
            height .7rem
            line-height .7rem
            font-size .4rem
            font-weight bold
            &_active
                border-color #f07
                animation evasive .7s infinite
.slide-enter-active {
  animation: bounce-in .3s;
}
.slide-leave-active {
  animation: bounce-out .3s;
}
@keyframes evasive {
    from {
        border-color #f07
    }
    to{
        border-color #aaa
    }
}
@keyframes bounce-in {
  0% {
    opacity 0
    transform: translate(100%,0);
  }
  100% {
    transform: translate(0,0);
    opacity 1
  }
}
@keyframes bounce-out {
  0% {
    transform: translate(0,0) ;
    opacity 1
  }
  100% {
    opacity 0
    transform: translate(-100%,0);
  }
}
</style>



