<script>
export default {
    name:'flashsalerule',
    data(){
        return {
            Curtainshow:true,
            rules:[
            ],
            title:'活动规则'
        }
    },
    methods:{
        curtainClose(){
            this.Curtainshow = false;
            this.rules = [],
            this.title = '活动规则'
            this.$el.parentNode.removeChild(this.$el);
        }
    }
}
</script>
<template>
    <OMCurtain 
    :show="Curtainshow"
    @close="curtainClose"
    >
        <div class="rules">
            <div class="rules-title">{{title}}</div>
            <div class="rules-content">
                <p 
                v-for="(i,k) in rules" 
                :key="k"
                class="rules-detail"
                >
                    {{i}}
                </p>
            </div>
        </div>
    </OMCurtain>
</template>
<style lang="stylus" scoped>
.rules
    position relative
    width 5.4rem 
    padding .4rem .5rem
    background-color #fff
    border-radius .2rem 
    margin 0 auto    
    &-title 
        font-size .32rem 
        font-weight bold
    &-detail 
        text-align left
        font-size .28rem 
        color #666
</style>



