<script>
export default {
    name:'OMTabpane',
    props:{
        value:Number,
        current:Number
    }
}
</script>
<template>
    <div 
    :class="['om-tab-pane',current!==value?'om-tab-pane__deactive':'']">
        <slot></slot>
    </div>
</template>


