import axios from "axios";
import qs from 'qs';
import omui from '../../src/index'
const {Toast,get} = omui
const BASEURL = 'https://mapi.fufentong.com/master/';
const service  = axios.create({
    baseURL:BASEURL,
    timeout:0
})
service.interceptors.request.use(
conf=>{
    let baseData = {
        token:get('fft-react')?get('fft-react').token:null
    }
    conf.method === 'get'?conf.params = Object.assign(baseData,conf.params):conf.data = qs.stringify(Object.assign(baseData,conf.data));
    conf.headers['Content-Type'] = 'application/x-www-form-urlencoded';

    return conf;
},err=>{
    Toast(err);
    Promise.reject(err)
})

service.interceptors.response.use(
    res=>{
        if(res.data.status===200){
            return res.data
        }else if(res.data.status===1001){
            Toast(res.data.msg);
            return res.data
        }
        else{
            Toast(res.data.msg);
            return res.data
        }
    },
    err=>{
        if(err){
            let text = '网络异常，请重试';
            Toast(text);
        }
        return Promise.reject(err)
    }
)
export default service;


