<template>
  <div id="app">
    <check-phone></check-phone>
    <transition :name="transitionName" mode='out-in'>
      <keep-alive include="index">
        <router-view class="router"></router-view>
      </keep-alive>
    </transition>
    <navbar></navbar>
    <BackTop></BackTop>
  </div>
</template>

<script>
import navbar from './components/layout/navbar'
import BackTop from './components/layout/scollTop'
import CheckPhone from './components/layout/checkphone'
export default {
  name: 'App',
  components:{
    navbar,
    BackTop,
    CheckPhone
  },
  data(){
    return{
      transitionName:'forward'
    }
  },
  mounted(){
    window.addEventListener('scroll',()=>{
        let scrollTop =  window.document.documentElement.scrollTop
        if(scrollTop>240){
            this.bus.$emit('backtop',true)
        }else{
            this.bus.$emit('backtop',false)
        }
    })
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'back' : 'forward'
    }
  }
}
</script>


<style lang="stylus">
body
  margin 0 
  padding 0
.demo 
  height 5px 
  width 7.50rem 
  background-color #000
.ac
  text-align center
  &.lg 
    font-size .50rem
.container
  padding .10rem
  p
    font-size .4rem
    font-weight bold
.forward-enter-active {
  animation: bounce-in .3s cubic-bezier(.36,.66,.04,1);
}
.forward-leave-active {
  animation: bounce-out .3s cubic-bezier(.36,.66,.04,1);
}
.back-enter-active {
  animation: back-in .3s cubic-bezier(.36,.66,.04,1);
}
.back-leave-active {
  animation: back-out .3s cubic-bezier(.36,.66,.04,1);
}
@keyframes bounce-in {
  0% {
    opacity 0
  }
  100% {
    opacity 1
  }
}
@keyframes bounce-out {
  0% {
    transform: translate(0,0) ;
    opacity 1
  }
  100% {
    opacity 0
    transform: translate(-100%,0) ;
  }
}
@keyframes back-in{
  0% {
    opacity 0
  }
  100% {
    opacity 1
  }
}
@keyframes back-out{
  0% {
    opacity 1
    transform: translate(0,0) ;
  }
  100% {
    transform: translate(100%,0) ;
    opacity 0
  }
}
</style>



