<script>
export default {
    name:'backTop',
    data(){
        return {
            show:false
        }
    },
    beforeCreate(){
        this.bus.$on('backtop',(res)=>{
            let route = this.$route.name
            if(route==='index'){
                this.show = res
            }
        })
    },
    methods:{
        BackTopFunc(){
            document.documentElement.scrollIntoView({
                behavior:'smooth'
            })
        }
    }
}
</script>
<template>
    <transition name="slide">
        <div class="backtop" v-show="show" @click="BackTopFunc">
            <i class="iconfont icon-verticalright backtop_icon"></i>
        </div>
    </transition>
</template>
<style lang="stylus" scoped>
.backtop
    width 40px 
    height 40px 
    line-height 40px
    text-align center 
    display block 
    border-radius 50%
    background-color rgba(0,0,0,0.6)
    position fixed 
    bottom 1.2rem
    right .25rem
    transform rotate(90deg)
    color #fff 
    &_icon 
        font-size .5rem
$--slide-transition = all cubic-bezier(.36,.66,.04,1) 300ms
.slide-enter,.slide-leave-to
    opacity 0 
    transform : translateX(100%)
.slide-enter-active,.slide-leave-active
    transition:$--slide-transition
</style>





