<script>
import { mapActions,mapState } from 'vuex'
export default {
    name:'navbar',
    methods:{
        LinkTo(path){
            this.$router.push(path)
        }
    },
    computed:{
        // ...mapState('layout',['showNav']),
        showNav(){
            let route =  this.$route.name
            let nav = ['index','user']
            if(nav.indexOf(route)>-1){
                return true 
            }else {
                return false 
            }
        },
        Path(){
            return this.$route.name
        }
    }
}
</script>
<template>
    <transition name="slide">
        <div class="nav" v-if="showNav">
            <div class="nav-container">
                <div 
                @click="LinkTo('/')"
                :class="['nav-item',Path==='index'?'nav-item__active':'']">
                    <i class="navicon iconfont icon-home"></i>
                    <span>首页</span>
                </div>
                <div 
                @click="LinkTo('/user')"
                :class="['nav-item',Path==='user'?'nav-item__active':'']">
                    <i class="navicon iconfont icon-user"></i>
                    <span>我的</span>
                </div>
            </div>
        </div>
    </transition>
</template>
<style lang="stylus" scoped>
.nav 
    position fixed
    width 100%
    height 1rem
    bottom 0 
    left 0
    z-index 999
    background-color #fff
    &-container
        width 100%
        height 100%
        display flex
    &-item 
        flex 1 
        text-align center
        padding .1rem 0
        display flex 
        flex-direction column
        justify-content space-around
        &__active 
            color #f07
        .navicon 
            font-size .48rem
$--slide-transition = all cubic-bezier(.36,.66,.04,1) 300ms
.slide-enter,.slide-leave-to
    opacity 0 
    transform : translateY(100%)
.slide-enter-active,.slide-leave-active
    transition:$--slide-transition
</style>



